<template>
	<yd-layout>
		<yd-navbar slot="navbar" title="审批意见列表">
			<a href="javascript:;" @click="goBack" slot="left">
				<yd-navbar-back-icon></yd-navbar-back-icon>
			</a>
		</yd-navbar>
		<scroller class="sc">
			<div v-for="(item,dataIndex) in data" class="contentBox">
				<div class="handlderSpot">
					<div class="adopt">
						<div class="line_color"></div>
						<div class="Ball1"></div>
						<div class="Ball2" :class="[item.checkStatus=='AGREE' ? 'bg-green' : 'bg-red']"></div>
					</div>
					<i class="yd-icon-left1"></i>
				</div>
				<p class="timeline-title">{{item.nodeName}}</p>
				<span v-if="item.handlerInfo.photoId" class="handlderImag">
		        		<img slot="img" :src="getFilePath(item.handlerInfo.photoId)"/>
		        </span>
				<span v-else class="handlderImag">
	            	<img slot="img" src="../../../static/img/hy_default.jpg"/>
	            </span>
				<p style="margin-top: 10px;">
					<ul>
						<li>
							<h1>办&nbsp;&nbsp;理&nbsp;&nbsp;人：</h1>
							<span>{{item.handlerInfo.handler}}</span>
						</li>
						<li>
							<h1>部　　门：</h1>
							<span>{{item.handlerInfo.deptName}}</span>
						</li>
						<li>
							<h1>开始时间：</h1>
							<span>{{item.createTime}}</span>
						</li>
						<li>
							<h1>结束时间：</h1>
							<span>{{item.completeTime}}</span>
						</li>
						<li>
							<h1>状　　态：</h1>
							<span :class="[item.checkStatus=='AGREE' ? 'color-green' : 'color-red']">{{item.checkStatusText}}</span>
						</li>
						<li>
							<h1>意　　见：</h1>
							<span v-html="item.remark"></span>
						</li>
						<li class="incidental">
							<h1>附　　件：</h1>({{item.attachments.length==0?'无':item.attachments.length}}) <span @click="showFile(item)" class="fileListBtn" v-if="item.attachments.length>0">{{toggleWord[item.toggleTag]}}</span>
	
							<ul v-show="item.show">
								<li v-for="file in item.attachments">
									<span v-if="getFileType(file)==1">
											<yd-lightbox>
												<yd-lightbox-img :src="getFilePath(file.fileId)"></yd-lightbox-img>
											</yd-lightbox>
									</span>
									<span v-else-if="getFileType(file)==2||getFileType(file)==3" @click="preViewOffice(file)">{{file.fileName}}</span>
									<span v-else @click="downloadFile(file)">
											<yd-icon slot="icon" name="file"></yd-icon>{{file.fileName}}
									</span>
								</li>
							</ul>
	
						</li>
					</ul>
				</p>
				</span>
	
				<div v-if="data.length==0" class="nodata">
					<yd-icon slot="icon" name="nodata"></yd-icon>
					<span class="noDataTips">无记录</span>
				</div>
			</div>
		</scroller>
	</yd-layout>
</template>

<script type="text/ecmascript-6">
	import Vue from 'vue';
	import { LightBox, LightBoxImg, LightBoxTxt } from 'vue-ydui/dist/lib.rem/lightbox';
	import router from '@/router/index';

	Vue.component(LightBox.name, LightBox);
	Vue.component(LightBoxImg.name, LightBoxImg);

	export default {
		data() {
			return {
				data: [],
				toggleWord: ['展开', '收起'],
				handerInfo: {}
			}
		},
		created() {
			let actInstId = this.$route.params.instId;
			var url = _baseUrl + '/bpm/core/bpmNodeJump/listForInstMobile.do?actInstId=' + actInstId;

			this.$ajax.get(url).then(response => {
				var list = response.data.data.reverse();
				for(var i = 0; i < list.length; i++) {
					list[i].show = false;
					list[i].toggleTag = 0;
				}
				this.data = list;
			})
		},
		methods: {
			getFilePath(file) {
				return _baseUrl + "/sys/core/file/previewFile.do?fileId=" + file.fileId;

			},
			downloadFile(file) {
				let url = _baseUrl + "/sys/core/file/previewFile.do?fileId=" + file.fileId;
				let _self = this;
				if(plus.os.name == "iOS") {
					_self.$dialog.alert({
						mes: "IOS安全机制，不提供下载."
					});
					return;
				}

				var dtask = plus.downloader.createDownload(url, {}, function(d, status) {
					// 下载完成
					if(status == 200) {
						let fullPath = plus.io.convertLocalFileSystemURL(d.filename)
						_self.$dialog.alert({
							mes: "成功下载，文件存储在：" + fullPath
						});
					} else {
						_self.$dialog.alert({
							mes: "下载失败:" + status
						});
					}
				});
				//dtask.addEventListener( "statechanged", onStateChanged, false );
				dtask.start();
			},
			preViewOffice(file) {
				let src = _baseUrl + "/scripts/PDFShow/web/viewer.html?file=" + _baseUrl + "/sys/core/file/download/" + file.fileId + ".do";
				router.push({
					name: 'preViewViaPdf',
					params: {
						"src": src
					}
				});
			},
			getFileType(file) {
				var extName = file.extName.toLowerCase();
				if(extName == "jpg" || extName == "png" || extName == "bmp" || extName == "jpeg" || extName == "gif") {
					return 1;
				} else if(extName == "pdf") {
					return 2;
				} else if(extName == "doc" || extName == "docx" || extName == "xls" || extName == "xlsx" || extName == "ppt" || extName == "pptx") {
					return 3;
				}
				return 4;
			},

			showFile(item) {
				item.show = !item.show;
				item.toggleTag = item.toggleTag == 0 ? 1 : 0;
			}

		}
	}
</script>

<style type="text/css" scoped>

	*{
		color: #666;
	}
	
	.sc>div{
		padding-bottom: 1rem;
	}
	
	.color-green{
		color: #00e079;
	}
	
	ul>li>div>img {
		width: 1.5rem;
		height: 1.5rem;
	}
	
	.fileListBtn {
		font-weight: bold;
		color: #065279;
	}
	
	.fileStyle {
		margin: 0.1rem;
	}
	
	.yd-icon-file:before {
		font-size: .5rem !important;
	}
	
	.yd-timeline-item {
		position: relative;
	}
	
	.contentBox {
		position: relative;
		background: #fff;
		padding: .12rem;
		margin: .12rem .2rem .2rem .6rem;
		border-radius: .12rem;
		transition: all .3s;
	}
	
	.contentBox>p ul{
		width: calc(100% - 1.2rem);
		padding-right: .12rem;
	}
	
	.contentBox>p ul li{
		padding:.06rem 0;
		font-size: .28rem;
		display: flex;
	}
	
	.contentBox>p ul li span{
		flex: 1;
	}
	
	.line {
		width: 1px;
		height: 100%;
		color: #ececec;
		position: absolute;
		top: 0;
		left: .2rem;
	}
	
	.incidental {
	}
	
	.incidental>ul {
		margin: .08rem 0 0 1.12rem;
		padding: .12rem;
		border: 1px solid #ececec;
		border-radius: .12rem;
	}
	
	.incidental>ul>li {}
	
	.incidental>ul>li>span {
		color: #0daaf6;
		line-height: 0.24rem;
	}
	
	.incidental>ul>li>.yd-icon-file {
		font-size: 0.28rem !important;
	}
	
	.handlderSpot {
		position: absolute;
		top: 50%;
		margin-top: -.2rem;
		left: -.5rem;
		width: .4rem;
		height: .4rem;
	}
	
	.handlderSpot>.adopt {
		position: absolute;
		top: 50%;
		left: 50%;
		width: .24rem;
		height: .24rem;
		margin: -.12rem 0 0 -.12rem;
	}
	
	.handlderSpot>.adopt>.Ball1 {
		height: 100%;
		width: 100%;
		border-radius: 4rem;
		background: #0daaf6;
		z-index: 9;
	}
	
	.handlderSpot>.adopt>.Ball2 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: .12rem;
		height: .12rem;
		margin: -.06rem 0 0 -.06rem;
		border-radius: 4rem;
		z-index: 10;
	}
	
	.line_color {
		width: 1px;
		height: 8rem;
		background: #0daaf6;
		position: absolute;
		bottom: 0;
		left: 50%;
		z-index: 8;
	}
	
	.handlderSpot>i {
		position: absolute;
		top: 50%;
		margin-top: -.21rem;
		left: .26rem;
	}
	
	.handlderSpot>i:before {
		color: #fff;
		font-size: .4rem;
	}
	
	.contentBox .timeline-title {
		text-align: center;
		font-size: 0.32rem;
	}
	
	.handlderImag {
		position: absolute;
		top: .8rem;
		right: .12rem;
		height: 1.2rem;
		width: 1.2rem;
		border-radius: .6rem;
		overflow: hidden;
		display: block;
	}
	
	.handlderImag>img {
		height: auto;
		width: 100%;
	}
	
	.yd-timeline-content>li {
		padding-top: 0;
	}
	
	.fileListBtn {
		color: #0daaf6;
		font-weight: normal;
	}
	
</style>


